/**
 * @file cart module
 * @author ryu
 */

export const REGISTER = 'REGISTER';

export const state = () => {
    return {
        /**
         * @type {boolean}
         */
        register: {
            step: 1,
            phone: '',
            user: '',
            password: ''
        }
    };
};

export const mutations = {
    [REGISTER](state, registerData) {
        state = Object.assign(state, registerData)
    }
};

export const actions = {

    /**
     *
     * @param {Function} commit commit
     * @param {Object}  registerData
     * 注册信息
     */
    setRegister({commit}, registerData) {
        commit(REGISTER, registerData);
    }
};
