import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router);
const index = () => import('./pages/index');
const stepNI = () => import('./pages/step-two');
const stepSann = () => import('./pages/step-san');
const success = () => import('./pages/success');
export default new Router({
    mode: 'hash',
    scrollBehavior(to, from, savedPosition) {
        return {x: 0, y: 0}
    },
    routes: [
        {
            path: "/",
            name: "index",
            component: index
        },
        {
            path: "/ni",
            name: "stepni",
            component: stepNI
        },
        {
            path: '/sann',
            name: "stepsann",
            component: stepSann
        },
        {
            path: '/success',
            name: 'success',
            component: success
        }
    ]
})